@import './variables.scss';

%items-center {
	display: flex;
	justify-content: center;
	align-items: center;
}

%items-center-between {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

%items-center-start {
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

%items-center-end {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

%container {
	width: 100%;
	max-width: $screen-desktop;
	min-width: 360px;
	padding: 0 24px;
	margin: 0 auto;
}

%section_container {
	@extend %container;
	padding: 56px 24px 88px 24px;

	@media screen and (min-width: $screen-tablet) {
		padding: 80px 24px  88px 24px;
	}

	@media screen and (min-width: $screen-desktop) {
		padding: 104px 24px 88px 24px;
	}
}

%button_primary {
	@extend %items-center;
	width: 100%;
	height: 100%;
	min-width: 44px;
	border-radius: 100px;
	cursor: pointer;
}

%circle_icon {
	width: 60px;
	height: 60px;
	border-radius: 50%;
	padding: 16px;
}